import React from 'react';
import { StaticQuery, graphql } from "gatsby"

import './style.scss';

// import SunLogo from '../images/sun.svg';
// import NavbarTop from './navbar-top';


export default () => (
	<StaticQuery
	query={graphql`
  {
    allGoogleSpreadsheetChatter {
      edges {
        node {
          intro
        }
      }
    }
  }
`}
  
  render={data => (

<div>
		{data.allGoogleSpreadsheetChatter.edges.map(({ node }, index) => (
	  		<div class="container">
        <div class="columns">
          <div class="column is-10 is-offset-1">
            <div class="box is-post-it">
                  <p class="is-size-5"><span dangerouslySetInnerHTML={{ __html: node.intro }}/></p>
             </div>
           </div>
          </div>
        </div>
			))}
	  </div>
	)}
  />
  )